<template>
  <div class="manage-visit">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="未处理异常" name="untreated"></el-tab-pane>
      <el-tab-pane label="已处理异常" name="treated"></el-tab-pane>
    </el-tabs>
    <el-form :inline="true" :model="formInline" ref="formInline">
      <el-form-item label="街路巷/小区">
        <!--<el-input v-model="formInline.communityName" size="small" placeholder="请输入" clearable></el-input>-->
        <el-select v-model="formInline.communityName" placeholder="请选择" clearable filterable size="small">
          <el-option
            v-for="(item,index) in communityList"
            :key="index"
            :label="item"
            :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="门牌号/楼栋号">
        <el-input v-model="formInline.buildingName" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
       <el-form-item label="单元">
        <el-input v-model="formInline.houseUnit" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="房(室)号">
        <el-input v-model="formInline.roomNo" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="异常类型">
        <el-select v-model="formInline.abnormalType" size="small" placeholder="请选择" clearable>
          <el-option label="用水异常" value="用水异常"/>
          <el-option label="用水疑似空转租" value="用水疑似空转租"/>
          <el-option label="空房购物" value="空房购物"/>
        </el-select>
      </el-form-item>
      <el-form-item label="房东姓名"  v-if="solveStatus == 1">
        <el-input v-model="formInline.landlordName" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="姓名"  v-if="solveStatus == 0">
        <el-input v-model="formInline.commonName" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="房屋特殊标注">
        <el-select
          size="small"
          v-model="formInline.houseSpecialMarkArr"
          multiple
          filterable
          allow-create
          default-first-option
          placeholder="请选择"
          @change="houseSpecialChange">
          <el-option-group
            v-for="group in houseSpecialMarkGroups"
            :key="group.label"
            :label="group.label">
            <el-option
              v-for="item in group.options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item label="房屋隐患" v-if="solveStatus == 0">
        <el-select v-model="formInline.judgeDanger" placeholder="请选择" clearable size="small">
          <el-option label="房屋存在隐患" value="1"/>
          <el-option label="房屋不存在隐患" value="0"/>
        </el-select>
      </el-form-item>
      <el-form-item label="走访人员" v-if="solveStatus == 0">
        <el-input v-model="formInline.visitPerson" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="核实人员" v-if="solveStatus == 0">
        <el-input v-model="formInline.checkPerson" size="small" placeholder="请输入" clearable></el-input>
      </el-form-item>
      <el-form-item label="走访时间" v-if="solveStatus == 0">
        <el-date-picker
          v-model="formInline.visitDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择日期时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click="onQuery">查询</el-button>
      </el-form-item>
    </el-form>
    <div v-loading="isLoadingData">
      <el-table :data="tableData" border >
        <el-table-column label="序号" type="index" :index="indexMethod" width="56" align="center"></el-table-column>
        <el-table-column prop="abnormalType" label="异常类型" width="80"></el-table-column>
        <!--<el-table-column prop="abnormalInfo" label="异常信息"></el-table-column>-->
        <el-table-column prop="standardScore" label="Z分数超出百分比" width="80"/>
        <el-table-column prop="shoppingTimes" label="购物次数" width="80"/>
        <el-table-column prop="emptyShopping" label="疑似空房购物" width="80"/>
        <el-table-column prop="emptyUseWater" label="疑似空房用水" width="80"/>
        <!--<el-table-column prop="specialGoods" label="购买特殊物品"/>-->
        <el-table-column prop="communityName" label="街路巷/小区" width="80"></el-table-column>
        <el-table-column prop="apartmentNum" label="门牌号/楼栋号" width="80" align="center"></el-table-column>
         <el-table-column prop="houseUnit" label="单元" width="70" align="center"></el-table-column>
        <el-table-column label="房(室)号" width="70" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="gotoRoomInfo(scope.row.proCollId)">{{ scope.row.roomNum }}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="houseStatus" label="房屋状态" align="center" width="80"></el-table-column>
        <el-table-column prop="houseDanger" label="房屋隐患情况" align="center" width="100"></el-table-column>
        <el-table-column prop="houseSpecialMark" label="房屋特殊标注" align="center" width="100"></el-table-column>
        <el-table-column align="center" label="房东信息" width="115">
          <template slot-scope="scope">
            <span>{{scope.row.landlordName}}</span> - <span>{{scope.row.landlordId}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="landlordPhone" label="房东联系方式" width="115" align="center"></el-table-column>
        <el-table-column prop="judgeAbnormalTime" label="判断时间" width="100" align="center"></el-table-column>
        <el-table-column prop="officerName" label="负责人" width="80" align="center"></el-table-column>
        <el-table-column prop="officerPhone" label="负责人联系方式" width="115" align="center"></el-table-column>
        <el-table-column prop="operate" label="操作" width="100" align="center">
          <template slot-scope="scope">
            <div v-if="activeName==='untreated'">
              <el-button type="text" @click="showPrintingDialog(scope.row.proCollId)">打印</el-button>
              <el-button type="text" @click="gotoFeedback(scope.row.proCollId, scope.row.id)">反馈</el-button>
            </div>
            <div v-else>
              <el-button type="text" @click="showResultDialog(scope.row.proCollId)">结果查询</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="text-align: right; padding: 20px 0;"
        background
        layout="total, prev, pager, next"
        @current-change="onCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        :total="totalSize">
      </el-pagination>
    </div>
    <printing-dialog :showType="showType" :printingId="printingId" ref="printingDialog"></printing-dialog>
    <visit-printing :showType="showType" :printingId="printingId" ref="visitPrinting"></visit-printing>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import PrintingDialog from './PrintingDialog'
import VisitPrinting from './visit-printing'
import Server from './server'

export default {
  components: { PrintingDialog, VisitPrinting},
  data() {
    return {
      activeName: 'untreated',
      formInline: {
        communityName: '',
        buildingName: '',
        houseUnit: '',
        roomNo: '',
        abnormalType: "",
        userName: '',
        landlordName: '',
        commonName:'',
        houseSpecialMarkArr: '',
        houseSpecialMark: '',
        judgeDanger: '',
        visitPerson: '',
        checkPerson: '',
        visitDate: '',
      },
      value: '',
      tableData: [],
      currentPage: 1,
      totalSize: 0,
      pageSize: 10,
      printingId: '',
      showType: '',
      solveStatus: 1,
      officerNo: '',
      communityList: [],
      houseSpecialMarkGroups: [{ //房屋特殊标记
        label: '',
        options: [{
          value: '无',
          label: '无'
        }]
      }, {
        label: '特殊标记',
        options: [{
          value: '重点人员',
          label: '重点人员'
        }, {
          value: '风险人员',
          label: '风险人员'
        }, {
          value: '涉案人员',
          label: '涉案人员'
        }]
      }],
      isLoadingData:false
    }
  },
  mounted() {
    // 新增搜索条件持久化
    let formValue = sessionStorage.getItem('formValue')
    if (formValue) {
      this.formInline = JSON.parse(formValue)
    }

    if (Cookies.get('officerNo')) {
      this.officerNo = Cookies.get('officerNo')
    }

    this.getTableList()
    Server.queryAllCommunity().then(res => {
      this.communityList = res.data
    })
  },
  methods: {
    /**
     * 房屋特殊标记change事件
     * @param event
     * @param item
     */
    houseSpecialChange(event, item) {
      //如果选中项为无，则先清空整个选择框
      // debugger
      if (event[event.length - 1] == "无") {
        this.formInline.houseSpecialMarkArr = ["无"]
      } else if (this.formInline.houseSpecialMarkArr.indexOf("无") != -1) {
        this.formInline.houseSpecialMarkArr.splice(this.formInline.houseSpecialMarkArr.indexOf("无"), 1);
      }
      // 将房屋特殊标记转化为字符串
      this.formInline.houseSpecialMark = this.formInline.houseSpecialMarkArr.join(",");
    },

    handleClick({ name }) {
      this.solveStatus = name === 'treated' ? 0 : 1
      this.$refs.formInline.resetFields();
      this.tableData = []
      this.totalSize = 0
      this.getTableList()
    },
    onQuery() {
      this.currentPage = 1
      this.getTableList()
      sessionStorage.setItem('formValue', JSON.stringify(this.formInline))

    },
    indexMethod(index) {
      return index + this.pageSize * (this.currentPage - 1) + 1
    },
    gotoRoomInfo(num) {
      this.$router.push({
        path: '/room-info',
        query: {
          proCollId: num
        }
      })
    },
    gotoFeedback(num, id) {
      this.$router.push({
        path: '/visit-feedback',
        query: {
          proCollId: num,
          id
        }
      })
    },
    showResultDialog(proCollId) {
      this.showType = 'result'
      this.printingId = proCollId
      this.$refs.printingDialog.show()
    },
    showPrintingDialog(proCollId) {
      this.showType = 'print'
      this.printingId = proCollId
      this.$refs.visitPrinting.show()
    },
    async getTableList() {
      this.isLoadingData = true
      await Server.getHouseInfo({
        communityName: this.formInline.communityName, //街路巷/小区
        apartmentNum: this.formInline.buildingName, //楼栋名称
        houseUnit: this.formInline.houseUnit, //单元
        roomNum: this.formInline.roomNo, //房（室）号
        abnormalType: this.formInline.abnormalType, //异常类型
        landlordName: this.formInline.landlordName, //房东姓名
        commonName: this.formInline.commonName, //房东姓名
        houseSpecialMark: this.formInline.houseSpecialMark, //房屋特殊标注
        judgeDanger: this.formInline.judgeDanger, //房屋隐患
        visitPerson: this.formInline.visitPerson, //走访人员
        checkPerson: this.formInline.checkPerson, //核实人员
        visitDate: this.formInline.visitDate, //走访时间
        solveStatus: this.solveStatus, //是否处理异常
        currentPage: this.currentPage, //
        totalSize: this.pageSize, //
        officerNo: this.officerNo //登录人的警号
      }).then(res => {
        this.tableData = res.data,
          this.totalSize = res.totalSize
      })
      this.isLoadingData = false
    },
    onCurrentChange(val) {
      this.currentPage = val
      this.getTableList()
    }
  }
}
</script>
<style scoped lang="less">
  body {
    margin: 0;
  }
</style>


