<template>
  <el-dialog
    top="5vh"
    width="900"
    title="打印核实单"
    :visible="dialogVisible"
    @close="handleClose">
    <div id="printBox" v-loading="printLoading">
      <div class="checkInfo" style="position: relative">
        <h4 style="border-bottom:1px solid #eee;padding:20px 0;margin:0">核实信息</h4>
        <section style="padding: 10px;">
          <b style="display: inline-block;width: 110px; text-align: right;margin-right:10px">房屋信息:</b>
          <span>{{!abnormalInfo? '':(abnormalInfo.communityName + '-' + abnormalInfo.apartmentNum + '' + abnormalInfo.houseUnit + '' + abnormalInfo.roomNum) }}</span>

        </section>
        <section style="padding: 10px;">
          <b style="display: inline-block;width: 110px; text-align: right;margin-right:10px">房主信息:</b>
          <span>{{!abnormalInfo ? '':(abnormalInfo.landlordName + ' ' + abnormalInfo.landlordId + ',联系方式' + abnormalInfo.landlordPhone)}}</span>
        </section>
        <section style="padding: 10px; overflow:hidden">
          <b style="float:left;width: 110px; text-align: right;margin-right:10px">入住人员信息:</b>
          <div style="float:left">
            <span>{{checkResidentInfo== null || checkResidentInfo.residentsInfo == null || checkResidentInfo.residentsInfo[0] == undefined ? "" : checkResidentInfo.residentsInfo[0].name + ' ' + checkResidentInfo.residentsInfo[0].idNumber + ',联系方式' + checkResidentInfo.residentsInfo[0].phone}}</span>
          </div>
        </section>
        <section style="padding: 10px;">
          <b style="display: inline-block;width: 110px; text-align: right;margin-right:10px">走访原因:</b>
          <span>{{!checkResidentInfo ? '':checkResidentInfo.visitReason }}</span>
        </section>
        <section style="padding: 10px; overflow:hidden">
          <b style="float:left;width: 110px; text-align: right;margin-right:10px">核实人员:</b>
          <div style="float:left">
            <span>{{!checkResidentInfo?'':checkResidentInfo.checkPersons}}</span>
          </div>
        </section>
        <section style="padding: 10px;">
          <b style="display: inline-block;width: 110px; text-align: right;margin-right:10px">走访人员:</b>
          <span>{{!checkResidentInfo ? '':contactMessage(checkResidentInfo.visitPersons) }}</span>
        </section>
      </div>
      <div class="checkResult">
        <h4 style="border-bottom:1px solid #eee;padding-bottom:20px;margin-bottom:0">核实结果</h4>
        <section style="padding: 10px 0;">
          <div style="display: inline-block; width: 100%">
            <div style="width: 33%; float: left;">
              <el-checkbox><b>寄住人员:</b></el-checkbox>
            </div>
            <div style="width: 33%; float: left;">
              <el-checkbox><b>房屋出租:</b></el-checkbox>
            </div>
            <div style="width: 33%; float: left;">
              <el-checkbox><b>其他:</b></el-checkbox>
            </div>
          </div>
          <div style="height:200px"></div>
        </section>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="onDownload" type="success">下载</el-button>
      <el-button size="small" @click="onPrintFun" type="success">打印</el-button>
      <el-button size="small" type="primary" @click="dialogVisible = false">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {Printd} from 'printd'
  import PeopleTable from './people-table'
  import Server from './server.js'
  import config from '@/config'

  export default {
    name: 'visitPrinting',
    components: {
      PeopleTable
    },
    props: {
      printingId: {
        type: [String, Number]
      },
      showType: {
        type: String
      }
    },
    data() {
      return {
        dialogVisible: false,
        infoList: {},
        data: [],
        switchType: '1',
        activeNames: [0],
        basicHouseInfo: {},
        abnormalInfo: {},
        checkFormResponses: [],
        checkResidentInfo: {},
        hiddenDanger: {
          houseDanger: "",
          personDanger: ""
        }, //房屋隐患
        printLoading: true
      };
    },
    methods: {
      getBasicHouseInfo() {

        if (this.abnormalInfo != null) {
          return
        }
      },
      handleChange(val) {
        // 点击折叠板后触发走访原因和房屋隐患的修改
        if (val != "") {
          this.hiddenDanger = this.checkFormResponses[val].hiddenDanger;
        }
      },
      handleClose(done) {
        this.dialogVisible = false
      },
      show() {
        this.dialogVisible = true
      },
      onPrintFun() {
        const d = new Printd()
        d.print(document.getElementById('printBox'))
      },
      async getServer(proCollId) {
        this.printLoading = true
        await Server.getFeedbackResult({
          proCollId
        }).then(res => {
          this.abnormalInfo = res.data.abnormalInfo
          this.checkFormResponses = res.data.checkFormResponses
          this.checkResidentInfo = res.data.checkResidentInfo
        })
        this.printLoading = false
      },
      splitMessage(str) {
        let result = '<span style="line-height:20px">' + str.split('；').join('</span><br><span style="line-height:20px">') + '</span>'
        return result
      },
      residentInfoMessage(residentInfo) {
        if (residentInfo == undefined) {
          return
        }
        var html = "";
        for (var attr in residentInfo[0]) {
          html += '<span style="line-height:20px">' + attr + ':</span><span style="line-height:20px">' + residentInfo[attr] + '</span><br>'
        }
        let result = html
        return result
      },
      contactMessage(visitPersons) {
        if (!visitPersons) {
          return
        }
        return visitPersons.join(',')
      },
      extractMessage(checkPersons) {
        let extractStr = ''
        checkPersons.forEach(item => {
          extractStr += item.name + '，'
          extractStr += '联系方式：' + item.contactInfo + '；'
        })
        return this.splitMessage(extractStr)
      },
      onDownload() {
        if (!this.printingId) return
        window.open(config.LOCAL + '/survey/visit/check/form/print?proCollId=' + this.printingId)
        this.dialogVisible = false
      },
      changeSwitchType: function (d) {
        this.switchType = d
      }
    },
    watch: {
      printingId(val) {
        this.getServer(val)
      }
    }
  }
</script>

