<script>
  import {Printd} from 'printd'
  import PeopleTable from './people-table'
  import Server from './server.js'

  export default {
    name: 'ResultDialog',
    components: {
      PeopleTable
    },
    props: {
      printingId: {
        type: [String, Number]
      },
      showType: {
        type: String
      }
    },
    data() {
      return {
        dialogVisible: false,
        infoList: {},
        data: [],
        switchType: '1',
        activeNames: [0],
        basicHouseInfo:{},
        abnormalInfo: {},
        checkFormResponses:[],
        checkResidentInfo:{},
        hiddenDanger: {}, //房屋隐患
        resultSearchLoading:false,
      };
    },
    methods: {
      handleChange(val) {
        // 点击折叠板后触发走访原因和房屋隐患的修改
        if (val != "") {
          this.hiddenDanger = this.checkFormResponses[val].hiddenDanger;
        }
      },
      handleClose(done) {
        this.dialogVisible = false
      },
      show() {
        this.dialogVisible = true
      },
      onPrintFun() {
        const d = new Printd()
        d.print(document.getElementById('printBox'))
      },
      async getServer(proCollId) {
        this.resultSearchLoading = true;
        Server.getFeedbackResult({
          proCollId
        }).then(res => {
          this.abnormalInfo = res.data.abnormalInfo
          this.checkFormResponses = res.data.checkFormResponses
          this.checkResidentInfo = res.data.checkResidentInfo
          this.resultSearchLoading = false;
        })
      },
      splitMessage(str) {
        let result = '<span style="line-height:20px">' + str.split('；').join('</span><br><span style="line-height:20px">') + '</span>'
        return result
      },
      contactMessage(visitPersons) {
        return visitPersons.join(',')
      },
      extractMessage(checkPersons) {
        let extractStr = ''
        checkPersons.forEach(item => {
          extractStr += item.name + '，'
          extractStr += '联系方式：' + item.contactInfo + '；'
        })
        return this.splitMessage(extractStr)
      },
      onDownload() {
        if (!this.printingId) return
        window.open('http://50.120.80.244/survey/visit/check/form/print?proCollId=' + this.printingId)
        this.dialogVisible = false
      },
      changeSwitchType: function (d) {
        this.switchType = d
      }
    },
    watch: {
      printingId(val) {
        this.getServer(val)
      }
    },
    computed: {
      dialogTitle() {
        return this.showType === 'print' ? '打印核实单' : '走访核实单'
      }
    }
  }
</script>

